<template>
	<view class="">
		<view class="title">
			<!-- #ifdef APP-PLUS -->
			<uni-status-bar bgcolor="transparent"></uni-status-bar>
			<!-- #endif -->
			<view class="title-text">
				{{i18n.Hash_card}}
			</view>
		</view>
		<view class="content">
			
			<view class="list animated bounceInUp fast">
				<view class="title">
					{{i18n.Counterparty_username}}
				</view>
				<view class="input-item">
					<input type="text" :disabled="isCheck" :placeholder="i18n.Please_enter_their_username" v-model="username" @blur="testUsername">
					<text v-if="isCheck">{{i18n.Verification_succeeded}}</text>
				</view>
			</view>
			
			<view class="list animated bounceInUp fast">
				<view class="title">
					{{i18n.Amount_transferred}}
				</view>
				<view class="input-item">
					<input type="number"  :placeholder="i18n.Please_enter_the_number_of_transfers" v-model="num">
				</view>
			</view>
			

			<view class="list animated bounceInUp fast">
				<view class="title">
					{{i18n.yanzhengma}}
				</view>
				<view class="input-item">
					<input type="text" :placeholder="i18n.enter_vcode" v-model="yzm" maxlength="6">
					<view>
						<vcode :account="phone"></vcode>
					</view>
				</view>
				<view class="list-input-tip">
					{{i18n.Current_remaining_hashrate_card}}:{{info.cash}}{{i18n.piece}}
				</view>
			</view>
			
			
			
			<view class="list-msg animated bounceInUp fast">
				{{i18n.sendCard_tip}}
			</view>
			
			
			<!-- <view class="list-tip animated bounceInUp fast">
				可用：{{curInfo.balance-(num*(1+curInfo.fee))}} {{curInfo.title_en}}
			</view> -->
			<button type="default" 
			:loading="loadings" 
			:disabled="shows" 
			class="flash-btn animated bounceInUp fast" 
			:class="{'active':!shows}" 
			@tap="comfirm">{{i18n.Transfer_now}}</button>
		</view>
		<pay-pwd
		:maxlength="6" 
		:isPwd="true" 
		:isSHow="showpwd" 
		:isShows="isshows"
		:isShowss="showss"
		@close="close"
		@input-again="inputAgain"
		@finish="finish"></pay-pwd>
		<load v-if="laoding"></load>
	</view>
</template>

<script>
	import {mapGetters} from "vuex";
	import uniStatusBar from '@/components/uni-status-bar/uni-status-bar.vue';
	import {pageto, showToast, fetch, forceUpdate} from '@/common/js/util.js';
	import Load from '@/components/common/load.vue';
	export default {
		components:{
			uniStatusBar,Load
		},
		data() {
			return {
				loadings:false,
				showpwd:false,
				showss:false,
				isshows:false,
				laoding:false,
				shows:true,
				username:'',
				yzm:'',
				num:'',
				isCheck:false,
				phone:'',
				check:false,
				info:{}
			}
		},
		watch:{
			username(val){
				this.change();
			},
			num(val){
				this.change();
			},
			yzm(val){
				this.change();
			}
		},
		onLoad(e) {
			this.info = JSON.parse(e.info)
			this.phone = this.getLoginInfo.email
		},
		onShow(){
			forceUpdate(this.getLangType);
		},
		computed:{
			i18n() {
				return this.$t('message')
			},
			...mapGetters([
				'getRequestUrl',
				'getLangType',
				'getLogin',
				'getLoginInfo'
			]),
		},
		methods:{
			comfirm(){
				if(!this.isCheck){
					showToast(this.i18n.sendCard_tip00)
					return
				}

				if(this.shows) return;
				this.loadings = true;
				this.showpwd  = true;
			},
			change(){
				if(this.username && this.num && this.yzm){
					this.shows = false;
					return true;
				}else{
					this.shows = true;
					return false;
				}
			},
			close(val){
				if(val==1){
					this.isshows = false;
					this.showss = false;
					this.showpwd = false;
				}else{
					this.isshows = val;
					this.showpwd = val;
					this.showss = val
				}
			},
			inputAgain(val){
				this.isshows = false;
				this.showpwd = true;
			},
			finish(val){
				let data = {
					id:this.getLoginInfo.id,
					token:this.getLoginInfo.token,
					suser:this.username,
					num:this.num,
					paypwd:val,
					yzmtype:1,
					yzm:this.yzm
				}
				this.laoding = true;
				this.loadings = false;
				fetch('/api/wallet/djqZ',data,'POST').then(res=>{
					this.laoding = false;
					showToast(res.data.msg)
					if(res.data.code){
						this.isshows = false;
						this.showss = false;
						this.shows = false;
						this.num='';
						this.yzm='';
						this.username='';
						setTimeout(()=>{
							uni.navigateBack({
								delta:1
							})
						},500)
					}else{
						this.isshows = false;
						this.showss = false;
						this.shows = false;
						if(res.data.msg=='支付密码错误！'){
							this.isshows = true;
						}else if(res.data.msg=='usdt不足'){
							this.isshows = false;
							this.shows = false;
							this.showss = true;
						}
					}
				}).catch(error=>{
					this.laoding = false;
				})
			},
			//检查用户是否存在
			testUsername(e){
				this.check = true;
				console.log(e.detail.value);
				let data = {
					id:this.getLoginInfo.id,
					token:this.getLoginInfo.token,
					suser:e.detail.value
				}
				fetch('/api/wallet/checkGasZ',data,'post')
					.then(res=>{
						showToast(res.data.msg)
						this.check= false;
						if(res.data.code){
							
							this.isCheck = true;
							return
						}
						this.isCheck = false;
					})
					.catch(err=>{
						this.isCheck = false;
						this.check= false;
					})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '@/common/scss/variable.scss';
	.title{
		background: #1D184F;
		color: $white;
		.title-text{
			line-height: 78upx;
			text-align: center;
		}
		.title-info{
			padding: 48upx 0 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.item{
				flex: 1;
				padding-bottom: 20upx;
				text-align: center;
				font-size: 34upx;
				color: $white;
				position: relative;
				&.active{
					color: #5631FD;
					&::after{
						content: "";
						position: absolute;
						left: 50%;
						bottom: 0;
						transform: translateX(-50%);
						height: 3upx;
						width: 160upx;
						background-color: #5631FD;
					}
				}
			}
		}
	}
	.content{
		padding-top: 20upx;
		.list{
			padding: 36upx 30upx 0;
			background-color: #1a144a;
			&:last-of-type{
				.input-item{
					border-bottom: none;
				}
			}
			.title{
				font-size: 30upx;
				color: $white;
				background: #1a144a;
			}
			.list-input-tip{
				color: #8884B7;
				font-size: 24upx;
				display: block;
				padding: 20upx 0 40upx; 
			}
			.input-item{
				display: flex;
				justify-content: space-between;
				align-items: center;
				border-bottom: 1upx solid #2B2860;
				input{
					line-height: 88upx;
					height: 88upx; 
					font-size: 28upx;
				}
				text{
					font-size: 28upx;
				}
			}
		}
		.list-msg{
			font-size: 24upx;
			color: #F12323;
			padding: 20upx 30upx;
		}
		.list-tip{
			background-color: #1a144a;
			padding: 20upx 0 20upx 30upx;
			font-size: 24upx;
			color: #8884B7;
			&:last-of-type{
				padding-bottom: 40upx;
			}
		}
		.flash-btn{
			margin: 50upx 30upx;
			height: 100upx;
			line-height: 100upx;
			text-align: center;
			font-size: 34upx;
			background: #8884B7;
			color: #0F0C39;
			&.active{
				background: linear-gradient(to right,#7965E0,#562FE8);
				color: $white;
			}
		}
	}
</style>
